table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }
  .copy_btn{
    position: absolute;
    top: -15px;
    right: 15px;
}
ul.color_plates li {
  list-style: none;
}

ul.color_plates li span {
  display: inline-block;
  width: 30px;
  cursor: pointer;
  height: 30px;
  background: white;
  border-radius: 50%;
  margin: 3px 8px;
}
ul.color_plates li .green{
  background-color: green;
}
ul.color_plates li .blue{
  background-color: blue;
}
ul.color_plates li .purple{
  background-color: purple;
}

ul.color_plates {
  display: flex;
  margin: 0;
  padding: 0;
  margin-right: 30px;
}